.textarea-field-container .mdc-notched-outline__notch .mdc-floating-label,
.textarea-field-container .mdc-text-field-helper-line .mdc-text-field-character-counter {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


.textarea-field-container .mdc-text-field--focused:not(.mdc-text-field--disabled) .mdc-floating-label {
    color: var(--mdc-theme-primary);
}